.movie-card{
	width: 350px;
    height: 20px;
    padding: 10px;
    margin-top: 5px;
    text-align: center;
    font-weight: bold;
    background: #e0dede;
    border-radius: 10px;
    box-shadow: 5px 20px 30px #000;
    cursor: pointer;
}

.movie-card-titles{
     font-weight: bold;
     color: white;
     font-size: 2.5em;
     padding-bottom: 30px;
     justify-content: center;
    display: flex;


}